@import url("https://fonts.googleapis.com/css?family=Signika+Negative:300,400");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700;900&display=swap");
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}

p {
  font-size: 2rem;
  color: #000;
}

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1,
h2,
h3 {
  margin: 0;
}

h1 {
  font-size: 4rem;
  font-weight: 900;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

#rotatingWords {
  display: inline-block;
}

.typeText {
  margin: 0;
}

.old {
  display: none;
}

.gsapCursor.blink {
  display: inline-block;
  animation: 1s blinkCursor step-end infinite;
}

.gsapCursor.hide {
  display: none;
}

@keyframes blinkCursor {
  from, to {
    color: transparent;
  }
  50% {
    color: black;
  }
}
.bg-foto--1 {
  background-image: url("https://images.unsplash.com/photo-1518791841217-8f162f1e1131?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1350&q=80");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  height: 100vh;
}

.menu-bar {
  background-image: linear-gradient(-90deg, #d38312, #a83279);
}

.nav-link {
  font-size: 16px;
  margin: 3px;
  color: #fff !important;
  font-weight: 500;
}

.navbar-nav {
  text-align: right;
  margin-right: 20px;
}

.menu-bar .navbar-toggler {
  padding-right: 20px;
  outline: none !important;
  border: none !important;
}

.navbar .fa {
  font-size: 26px;
  color: #fff !important;
}

.nav-link:hover {
  font-weight: 600;
  border-bottom: 1px solid #fff;
  padding-bottom: 1px;
}

.navbar-brand img {
  /*width: 150px;*/
  margin-top: 5px;
  margin-left: 20px;
}

@media only screen and (max-width: 1000px) {
  .nav-link:hover {
    border-bottom: none !important;
  }
}
